import React, { Component } from 'react';
import { connect } from 'react-redux'
import './QuestionnaireViewer.styles.scss'
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import InputBox from '../../InputBox/InputBox.component'

import { zipCode, eBill, contact, name, no, yes } from '../../../redux/quoteForm/quoteForm.actions';
import { moveBackwardsAction, moveForwardsAction } from '../../../redux/questionnaireStage/questionnaireStage.actions'
import QuestionnaireSubTitle from '../../QuestionnaireSubTitle/QuestionnaireSubTitle.component';
import QuestionnaireCardHome from '../../QuestionnaireCards/QuestionnaireCardHome/QuestionnaireCardHome.component';
import QuestionnaireCardBill from '../../QuestionnaireCards/QuestionnaireCardBill/QuestionnaireCardBill.component';
import QuestionnaireCardContact from '../../QuestionnaireCards/QuestionnaireCardContact/QuestionnaireCardContact.component';
import QuestionnaireCardName from '../../QuestionnaireCards/QuestionnaireCardName/QuestionnaireCardName.component';

class QuestionnaireViewer extends Component {

    constructor(props) {
        super(props)
    }
    
    render() {

        const { onClick, ownsHome, zipCode, eBill, contact, name, zipCodeAction, yesAction, noAction, eBillAction, contactAction, nameAction, question, questionnairePosition, moveBackwardsAction, moveForwardsAction } = this.props

        return (
            <section className='QuestionnaireViewer' onClick={onClick} >
                <section className='QuestionnaireViewer__Container' onClick={e => e.stopPropagation()} >
                    {
                        questionnairePosition === 0 ? <QuestionnaireCardHome noAction={noAction} yesAction={ yesAction} zipCodeAction={zipCodeAction} moveForwardsAction={moveForwardsAction} /> :
                        ownsHome === false ? 
                        <section className='QuestionnaireViewer__Container__Blur'>
                            <QuestionnaireTitle >Sorry, Solar only works if you are a Homeowner.</QuestionnaireTitle>
                        </section> :
                        questionnairePosition === 1 ? <QuestionnaireCardBill moveForwardsAction={moveForwardsAction} moveBackwardsAction={moveBackwardsAction} eBillAction={eBillAction} /> : 
                        questionnairePosition === 2 ? <QuestionnaireCardContact  moveForwardsAction={moveForwardsAction} moveBackwardsAction={moveBackwardsAction} contactAction={contactAction} /> :
                        questionnairePosition === 3 ? <QuestionnaireCardName  moveForwardsAction={moveForwardsAction} moveBackwardsAction={moveBackwardsAction} nameAction={nameAction} /> : 
                        questionnairePosition === 4 ? 
                            <section className='QuestionnaireViewer__Container__Blur'>
                                <QuestionnaireTitle >Thank you, we will contact you shortly!</QuestionnaireTitle>
                            </section>  : null

                    }
                    <p className='QuestionnaireViewer__Container__X' onClick={onClick} >X</p>
                </section>
            </section>
        );
    }
};

const mapStateToProps = ({ quoteForm: { ownsHome, zipCode, eBill, contact, name }, questionnaireStage: { questionnairePosition }}) => ({
    ownsHome,
    zipCode,
    eBill,
    contact,
    name,
    questionnairePosition
})

const mapDispathToProps = dispatch => ({
    zipCodeAction: (zipNumber) => dispatch(zipCode(zipNumber)),
    eBillAction: (billAmount) => dispatch(eBill(billAmount)),
    contactAction: (contactOptions) => dispatch(contact(contactOptions)),
    nameAction: (names) => dispatch(name(names)),
    noAction: () => dispatch(no()),
    yesAction: () => dispatch(yes()),
    moveForwardsAction: () => dispatch(moveForwardsAction()),
    moveBackwardsAction: () => dispatch(moveBackwardsAction())
})

export default connect(mapStateToProps, mapDispathToProps)(QuestionnaireViewer);