import React, { Component } from 'react';
import { connect } from 'react-redux';
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import './QuestionnaireCardBill.styles.scss';
import CTA from '../../CTA/CTA.component';

class QuestionnaireCardBill extends Component {
    constructor(props) {
        super(props);
        this.eBillInput = React.createRef();
        this.defaulBilltValue = '100'
    }

    componentDidMount() {
        return this.props.eBillAction(this.defaulBilltValue)
    }

    render() {

        const { moveBackwardsAction, moveForwardsAction, eBillAction, eBill } = this.props

        return (
            <section className='QuestionnaireCardBill' >
                <section className='QuestionnaireCardBill__Blur' >
                    <section className='QuestionnaireCardBill__Blur__Container' >
                        <section className='QuestionnaireCardBill__Blur__Container__Top' >
                            <QuestionnaireTitle >What is your average Electrical Bill?</QuestionnaireTitle>
                        </section>
                        <section className='QuestionnaireCardBill__Blur__Container__Bottom' >
                        <p className='QuestionnaireCardBill__Blur__Container__Counter' >
                            {`$${eBill}`}
                        </p>
                            <input ref={this.eBillInput} id='eBillRange' className='QuestionnaireCardBill__Blur__Container__Bottom__Input' defaultValue={this.defaulBilltValue} type='range' min='0' max='1500' onChange={(e) => eBillAction(e.target.value)} />
                            <CTA onClick={moveForwardsAction} >Next</CTA>
                        </section>
                    </section>
                </section>
                
            </section>
        );
    }
};

const mapStateToProps = ({ quoteForm: {eBill} }) => ({
    eBill
})

export default connect(mapStateToProps)(QuestionnaireCardBill);