import { combineReducers } from 'redux';

import modalReducer from './modal/modal.reducer';
import quoteFormReducer from './quoteForm/quoteForm.reducer';
import cardReducer from './card/card.reducer';
import ticketReducer from './ticket/ticket.reducer';
import windowWidthReducer from './windowWidth/windowWidth.reducer';
import questionnaireStageReducer from './questionnaireStage/questionnaireStage.reducer';

export default combineReducers({
    modal: modalReducer,
    quoteForm: quoteFormReducer,
    card: cardReducer,
    ticket: ticketReducer,
    windowWidth: windowWidthReducer,
    questionnaireStage: questionnaireStageReducer
});