import questionnaireStageTypes from './questionnaireStage.types';
const { MOVEBACKWARDS, MOVEFORWARDS } = questionnaireStageTypes;

const initialStage = {
    questionnairePosition: 0
}

const questionnaireStageReducer = (state = initialStage, action) => {
    switch (action.type) {
        case MOVEBACKWARDS:
            if (state.questionnairePosition === 0) {
                return state;
            } else {
                return {
                    ...state,
                    questionnairePosition: state.questionnairePosition - 1
                };
            };
        case MOVEFORWARDS:
            if (state.questionnairePosition >= 4) {
                return state
            } else {
                return {
                    ...state,
                    questionnairePosition: state.questionnairePosition + 1
                };
            };
        default:
            return state;
    }
}

export default questionnaireStageReducer;